<template>
<!--pages/ShoppingCart/ShoppingCart.wxml-->
<view class="container">
  <view class="prod-list">
   <block v-for="(shopCartItem, shopCartItemIndex) in shopCartItemDiscounts" :key="shopCartItemIndex">
    <view class="section">
    	<image style="width: 130rpx;height: 130rpx;" :src="shopCartItem.user.avatarImage?serverUrl+shopCartItem.user.avatarImage:'../../static/images/icon/head.png'"></image>
    	<text>{{shopCartItem.user.username}}</text>
    </view>
	<block v-for="(item, index) in shopCartItem.carts" :key="index">
      <view class="prod-block">
       <view class="item">
         <view class="btn">
           <label>
             <checkbox @tap="onSelectedItem" :data-shopCartItemIndex="shopCartItemIndex" :data-index="index" :value="''+item.id" :checked="item.checked" color="#105c3e"></checkbox>
           </label>
         </view>
         <view class="prodinfo">
           <view class="pic">
             <image :src="serverUrl+item.image"></image>
           </view>
           <view class="opt">
             <view class="prod-name">{{item.title}}</view>
             <view class="price-count">
               <view class="price">
                 <text class="symbol">ï¿¥</text>
                 <text class="big-num">{{wxs.parsePrice(item.price)[0]}}</text>
                 <text class="small-num">.{{wxs.parsePrice(item.price)[1]}}</text>
               </view>
               <view class="m-numSelector">
                 <view @tap="onCountMinus" class="minus" :data-shopCartItemIndex="shopCartItemIndex"  :data-index="index"></view>
                 <input type="number" :value="item.num" disabled></input>
                 <view @tap="onCountPlus" class="plus" :data-shopCartItemIndex="shopCartItemIndex" :data-index="index"></view>
               </view>
             </view>
           </view>
         </view>
       </view>
      </view>
    </block>
	<view style="height: 20rpx;"></view>
   </block>
  </view>

  <view class="empty" v-if="shopCartItemDiscounts.length==0">
    <view class="img">
      <image src="/static/images/tabbar/ShoppingCart.png"></image>
    </view>
    <view class="txt">
      æ¨è¿æ²¡ææ·»å ä»»ä½ååå¦~
    </view>
  </view>

  <!-- åºé¨æé® -->
  <view class="cart-footer" v-if="shopCartItemDiscounts.length>0">
    <view class="btn all">
      <label @tap="onSelAll">
        <checkbox :checked="allChecked" color="#f7d731;"></checkbox>å¨é</label>
    </view>
    <view class="btn del" @tap="onDelBasket">
      <text>å é¤</text>
    </view>
    <view class="btn total">
      <view class="finally">
        <text>åè®¡:</text>
        <view class="price">
          <text class="symbol">ï¿¥</text>
          <text class="big-num">{{wxs.parsePrice(finalMoney)[0]}}</text>
          <text class="small-num">.{{wxs.parsePrice(finalMoney)[1]}}</text>
        </view>
      </view>
      <view class="total-msg" v-if="subtractMoney>0">
        æ»é¢:ï¿¥{{wxs.toPrice(totalMoney)}} ç«å:ï¿¥{{wxs.toPrice(subtractMoney)}}
      </view>
    </view>
    <view class="btn settle" @tap="toFirmOrder">
      <text>ç»ç®</text>
    </view>
  </view>
  <!-- end åºé¨æé® -->

</view>
</template>

<script module="wxs" lang="wxs" src="../../wxs/number.wxs"></script>

<script>
// pages/ShoppingCart/ShoppingCart.js
var http = require("../../utils/http.js"); 
var config = require("../../utils/config.js");
const Big = require("../../utils/big.min.js");

export default {
  data() {
    return {
      // picDomain: config.picDomain,
      shopCartItemDiscounts: [
		  {
			  id: 0,
			  image: "",
			  num: 0,
			  price: 0,
			  skuId: 0,
			  title: "",
			  userId: 0
		  }
	  ],
      finalMoney: 0,
      totalMoney: 0,
      subtractMoney: 0,
      allChecked: true,
	  serverUrl: config.domain,
	  shopCartItem: [],
    };
  },

  components: {},
  props: {},

  /**
   * çå½å¨æå½æ°--çå¬é¡µé¢å è½½
   */
  onLoad: function (options) {},

  /**
   * çå½å¨æå½æ°--çå¬é¡µé¢åæ¬¡æ¸²æå®æ
   */
  onReady: function () {},

  /**
   * çå½å¨æå½æ°--çå¬é¡µé¢æ¾ç¤º
   */
  onShow: function () {
     //å è½½è´­ç©è½¦
    this.carList();
    http.getCartCount(); //éæ°è®¡ç®è´­ç©è½¦æ»æ°é
  },
  methods: {
	  carList: function() {
		  uni.showLoading();
		  var params = {
		    url: "/cart/list",
		    method: "POST",
			needToken: true,
		    data: {
		  	  "pageNum": 1,
		  	  "pageSize": 10
		    },
		    callBack: res => {
		      if (res.data.total > 0) {
		        // é»è®¤å¨é
		        var shopCartItemDiscounts = res.data.list;
		        shopCartItemDiscounts.forEach(shopCartItemDiscount => {
					shopCartItemDiscount.carts.forEach(cart => {
						cart.checked = true;
					});
		        });
		        this.setData({
		          shopCartItemDiscounts: shopCartItemDiscounts,
		          allChecked: true
		        });
		      } else {
		        this.setData({
		          shopCartItemDiscounts: []
		        });
		      }
		  
		      this.calTotalPrice(); //è®¡ç®æ»ä»·
		  
		      uni.hideLoading();
		    }
		  };
		  http.request(params);
	  },
    /**
     * å»ç»ç®
     */
    toFirmOrder: function () {
      var shopCartItemDiscounts = this.shopCartItemDiscounts;
      var ShoppingCartIds = [];
      shopCartItemDiscounts.forEach(shopCartItems=> {
		  var ShoppingCartProds = [];
		  var user = shopCartItems.user;
		  shopCartItems.carts.forEach(cart => {
			  if (cart.checked) {
			  	ShoppingCartProds.push(cart);
			  }
		  });
		  var shopCartItem = {
			  ShoppingCartProds: ShoppingCartProds,
			  user: user,
			  isAgent: shopCartItems.isAgent,
			  backgroundAgentId: shopCartItems.backgroundAgentId
		  }
		  ShoppingCartIds.push(shopCartItem);
      });

      if (!ShoppingCartIds.length) {
        uni.showToast({
          title: 'è¯·éæ©åå',
          icon: "none"
        });
        return;
      }

      uni.setStorageSync("ShoppingCartIds", ShoppingCartIds);
      uni.navigateTo({
        url: '/pages/submit-order/submit-order?orderEntry=0'
      });
    },

    /**
     * å¨é
     */
    onSelAll: function () {
      var allChecked = this.allChecked;
      allChecked = !allChecked; //æ¹åç¶æ

      var shopCartItemDiscounts = this.shopCartItemDiscounts;

      for (var i = 0; i < shopCartItemDiscounts.length; i++) {
		  var carts = shopCartItemDiscounts[i].carts;
		  for (var i = 0; i < carts.length; i++) {
			  var cItems = carts[i];
			  cItems.checked = allChecked;
		  }
      }

      this.setData({
        allChecked: allChecked,
        shopCartItemDiscounts: shopCartItemDiscounts
      });
      this.calTotalPrice(); //è®¡ç®æ»ä»·
    },

    /**
     * æ¯ä¸é¡¹çéæ©äºä»¶
     */
    onSelectedItem: function (e) {
	  var shopCartItemIndex = e.currentTarget.dataset.shopCartItemIndex;
      var index = e.currentTarget.dataset.index; // è·ådata- ä¼ è¿æ¥çindex

      var shopCartItemDiscounts = this.shopCartItemDiscounts; // è·åè´­ç©è½¦åè¡¨

      var checked = shopCartItemDiscounts[shopCartItemIndex].carts[index].checked; // è·åå½åååçéä¸­ç¶æ

      shopCartItemDiscounts[shopCartItemIndex].carts[index].checked = !checked; // æ¹åç¶æ

      this.setData({
        shopCartItemDiscounts: shopCartItemDiscounts
      });
      this.checkAllSelected(); //æ£æ¥å¨éç¶æ

      this.calTotalPrice(); //è®¡ç®æ»ä»·
    },

    /**
     * æ£æ¥å¨éç¶æ
     */
    checkAllSelected: function () {
      var allChecked = true;
      var shopCartItemDiscounts = this.shopCartItemDiscounts;
      var flag = false;

      for (var i = 0; i < shopCartItemDiscounts.length; i++) {
		  var carts = shopCartItemDiscounts[i].carts;
		  for (var i = 0; i < carts.length; i++) {
			  var cItems = carts[i];
			  if (!cItems.checked) {
			    allChecked = !allChecked;
			    flag = true;
			    break;
			  }
			   if (flag) {
			     break;
			   }
		  }
      }
      this.setData({
        allChecked: allChecked
      });
    },

    /**
     * è®¡ç®è´­ç©è½¦æ»é¢
     */
    calTotalPrice: function () {
      var shopCartItemDiscounts = this.shopCartItemDiscounts;
	  var shopCartItem = [];
      var finalMoney = 0;
      for (var i = 0; i < shopCartItemDiscounts.length; i++) {
        var cItems = shopCartItemDiscounts[i].carts;
		for (var j = 0; j < cItems.length; j++) {
			var cart = cItems[j]
			if (cart.checked) {
			  shopCartItem.push(cart);
			  finalMoney+= cart.price*cart.num;
			}
		}
       }
      this.setData({
            finalMoney: finalMoney,
            totalMoney: finalMoney,
            subtractMoney: 0,
			shopCartItem: shopCartItem
      });
    },

    /**
     * åå°æ°é
     */
    onCountMinus: function (e) {
	 var shopCartItemIndex = e.currentTarget.dataset.shopCartItemIndex;
     var index = e.currentTarget.dataset.index;
     var shopCartItemDiscounts = this.shopCartItemDiscounts;
	 var prodCount = shopCartItemDiscounts[shopCartItemIndex].carts[index].num;
      if (prodCount > 1) {
        this.updateCount(shopCartItemDiscounts,shopCartItemIndex,index, prodCount-1);
		return;
      }else {
		  uni.showToast({
		    title: 'å·²ç»æå°äº',
		    icon: "none"
		  });
	  }
    },

    /**
     * å¢å æ°é
     */
    onCountPlus: function (e) {
	  var shopCartItemIndex = e.currentTarget.dataset.shopCartItemIndex;
      var index = e.currentTarget.dataset.index;
      var shopCartItemDiscounts = this.shopCartItemDiscounts;
      this.updateCount(shopCartItemDiscounts,shopCartItemIndex, index, shopCartItemDiscounts[shopCartItemIndex].carts[index].num+1);
    },

    /**
     * æ¹åè´­ç©è½¦æ°éæ¥å£
     */
    updateCount: function (shopCartItemDiscounts,shopCartItemIndex, index, prodCount) {
      var ths = this;
      uni.showLoading({
        mask: true
      });
      var params = {
        url: "/cart",
        method: "PUT",
		needToken: true,
        data: {
          num: prodCount,
          id: shopCartItemDiscounts[shopCartItemIndex].carts[index].id
        },
        callBack: function (res) {
          shopCartItemDiscounts[shopCartItemIndex].carts[index].num = prodCount;
          ths.setData({
            shopCartItemDiscounts: shopCartItemDiscounts
          });
          ths.calTotalPrice(); //è®¡ç®æ»ä»·

          uni.hideLoading();
          http.getCartCount(); //éæ°è®¡ç®è´­ç©è½¦æ»æ°é
        }
      };
      http.request(params);
    },

    /**
     * å é¤è´­ç©è½¦åå
     */
    onDelBasket: function () {
      var ths = this;
      var shopCartItemDiscounts = this.shopCartItemDiscounts;
      var ShoppingCartIds = [];

      for (var i = 0; i < shopCartItemDiscounts.length; i++) {
        var cItems = shopCartItemDiscounts[i].carts;
        for (var j = 0; j < cItems.length; j++) {
        	var cart = cItems[j]
        	if (cart.checked) {
        	  ShoppingCartIds.push(cart.id);
        	}
        }
      }

      if (ShoppingCartIds.length == 0) {
        uni.showToast({
          title: 'è¯·éæ©åå',
          icon: "none"
        });
      } else {
        uni.showModal({
          title: '',
          content: 'ç¡®è®¤è¦å é¤éä¸­çåååï¼',
          confirmColor: "#eb2444",
          success(res) {
            if (res.confirm) {
              uni.showLoading({
                mask: true
              });
			  ShoppingCartIds.forEach(ShoppingCartId => {
				  var params = {
				    url: "/cart/"+ShoppingCartId,
				    method: "DELETE",
					needToken: true,
				    callBack: function (res) {
				      uni.hideLoading();
				    }
				  };
				  http.request(params);
			  });
			  ths.carList();
            }
          }

        });
      }
    }
  }
};
</script>
<style>
@import "./ShoppingCart.css";
</style>